	module.exports = {"data":{"markdownRemark":{"html":"<ol start=\"2017\">\n<li>június 9, hajnal 03:00: A BME Formula Racing Team elindul Németországba, hogy aztán 1200 km megtétele után megnyerje szezonja első versenyét!</li>\n</ol>\n<p>A VDE (Német Villamosmérnökök Egyesülete) idén is megrendezte E-Race névvel felkészülési versenyét, amelyre neves német csapatok mellett egyedüli magyarként a BME FRT is meghívást kapott. Habár a Formula Student naptárába hivatalosan nem számít bele, a hétvége mégis a hivatalos szabályzat szerint zajlott. Elsőként a gépátvételt kellett teljesítenünk, hogy később a dinamikus versenyszámokon indulhassunk. A hosszútávú verseny, a gyorsulás és a skid pad mellett idén megjelentek a statikus versenyszámok, a mérnöki tervezés és az üzleti terv prezentáció is.</p>\n<p>Péntek délután, 15 óra hosszú és fárasztó utazás után érkeztünk meg Németországba, azon belül is az Itzehoe melletti Hungriger Wolf reptérre, ahol a hétvége eseményei zajlottak. Gyors kipakolás után már el is kezdődhetett a gépátvétel, amely ebben az évben a megszokottnál is szigorúbb volt, de szombatra az összes kritériumot teljesíteni tudtuk.</p>\n<p>Szombat délben az elektromos csapatok közül elsőként sikerült teljesítenünk a gépátvétel összes állomását, így már mehettünk is a tesztpályára, ahol kipróbálhattuk a legújabb FRT-s autót. Ezen a napon volt lehetőség a skid pad és a gyorsulás versenyszámokban is teljesíteni az idei év első éles, mért idejeit, miközben megkezdődtek a statikus megmérettetések is. Elsőként a mérnöki tervezésen szerepeltünk, ahol a futómű és a váz tervezés folyamatát mutattuk be, majd később az üzleti terv prezentációnkat hallgathatta meg a zsűri és az érdeklődők. Érdekesség, hogy míg a hagyományos versenyeken a statikus események zárt körűek, itt, hogy a csapatok tanulhassanak egymástól, minden esemény nyitott volt a csapatok és az érdeklődők számára.</p>\n<p>A szombat este egy kisebb díjátadóval végződött, ahol az aznapi versenyszámok eredményeit tudhattuk meg. Számunkra nagyon boldogan zárult a nap, hiszen az üzleti terv prezentációjával és skid pad-en másodikak lettünk, a mérnöki tervezést és a gyorsulást pedig megnyertük!</p>\n<p>A szombati ünneplés után nem pihentünk, hiszen hátra volt még a legtöbb pontot érő, hosszútávú versenyszám. Ezt a 8 induló csapatból összesen 4 tudta teljesíteni, jó visszajelzés, hogy már a szezon korai időszakában hiba nélkül teljesítettük a versenyszámot és az autó állapotát a saját fejlesztésű telemetria rendszerünkkel végig követni tudtuk. Végül második helyen zártunk, így a délutáni eredményhirdetésen a dobogó legfelső fokára állhattunk az összetett versenyt tekintve!\nNem volt már hátra más, csak a hazaút és a következő időszak megtervezése.</p>\n<p>Habár csak nemrég jöttünk haza a szezon első versenyéről, amelyet a legnagyobb sikerrel, az összetettbeli első hellyel zártunk, koránt sem pihenhetünk még! Alig 1 hónap múlva ismét rajthoz állunk, ezúttal a szezon egyik legerősebb versenyén, az FS East-en, ahol a legjobbakkal mérhetjük össze tudásunkat. Addig azonban minden percet kihasználunk, a tesztidőszak javában folyik és a statikus versenyszámok esetén is igyekszünk mindenre felkészülni, hogy a következő versenyen is hasonlóan szép eredményekkel térhessünk haza!</p>\n<p>Mindez persze nem sikerülhetett volna támogatóink nélkül, akiknek ezúton is köszönjük a rengeteg segítséget, türelmet és munkát!</p>\n<p><a href=\"https://www.facebook.com/vdeerace/videos/781172198725337/?hc_ref=PAGES_TIMELINE\">Szombati összefoglaló</a></p>\n<p><a href=\"https://www.facebook.com/vdeerace/videos/781630852012805/?hc_ref=PAGES_TIMELINE\">Vasárnapi összefoglaló</a></p>","timeToRead":2,"excerpt":"június 9, hajnal 03:00: A BME Formula Racing Team elindul Németországba, hogy aztán 1200 km megtétele után megnyerje szezonja első versenyét…","frontmatter":{"title":"Címvédés a szezon első versenyén","cover":{"relativePath":"posts/2017-06-23/team.png"}},"fields":{"slug":"/cimvedes-a-szezon-elso-versenyen"}}},"pathContext":{"slug":"/cimvedes-a-szezon-elso-versenyen"}}

